import $ from 'jquery'

export default {
  components: {

  },
  data () {
    return {
      username: '',
      password: '',
      a: 3
    }
  },
  methods: {
    btnclick () {
      if (this.a >= 0) {
        if (this.username === 'CL' && this.password === '0717') {
          $('audio#cake-music')[0].play()
          let url = '/cake'
          this.$router.push({path: url})
        } else {
          this.a--
          alert(`不对，再想想!你还有${this.a}次机会`)
        }
      } else {
        alert('傻子，答案是你的名字和生日')
      }
    }
  }
}



